import React, { useEffect, useState } from 'react';
import Sidebar from '../components/Sidebar';
import { getReservations } from '../services/reservation';
import { Reservation } from '../types/reservation';
import { toast } from 'react-toastify';

const Dashboard: React.FC = () => {
  const [reservations, setReservations] = useState<Reservation[]>([]);
  const [loading, setLoading] = useState<boolean>(true);

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);
        const reservationsData = await getReservations();
        setReservations(reservationsData);
      } catch (error) {
        toast.error('Erro ao carregar as reservas!');
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  return (
    <div style={{ display: 'flex', fontFamily: 'Arial, sans-serif' }}>
      <Sidebar />
      <div style={{ marginLeft: '250px', padding: '20px', width: '100%' }}>
        <h1>Dashboard</h1>

        {/* Resumo de Reservas */}
        <div style={{ display: 'flex', justifyContent: 'space-between', marginTop: '20px' }}>
          <div style={{ padding: '20px', border: '1px solid #ddd', borderRadius: '5px', width: '48%' }}>
            <h3>Total de Reservas</h3>
            <p>{reservations.length} reservas realizadas</p>
          </div>
        </div>

        {/* Tabela de Reservas */}
        <div style={{ marginTop: '20px' }}>
          <h2>Últimas Reservas</h2>
          {loading ? (
            <p>Carregando...</p>
          ) : (
            <table style={{ width: '100%', borderCollapse: 'collapse' }}>
              <thead>
                <tr>
                  <th style={{ padding: '12px', textAlign: 'left', borderBottom: '1px solid #ddd' }}>Ambiente</th>
                  <th style={{ padding: '12px', textAlign: 'left', borderBottom: '1px solid #ddd' }}>Data</th>
                  <th style={{ padding: '12px', textAlign: 'left', borderBottom: '1px solid #ddd' }}>Status</th>
                  <th style={{ padding: '12px', textAlign: 'left', borderBottom: '1px solid #ddd' }}>Ações</th>
                </tr>
              </thead>
              <tbody>
                {reservations.slice(0, 5).map((reservation) => (
                  <tr key={reservation.id}>
                    <td style={{ padding: '12px', borderBottom: '1px solid #ddd' }}>{reservation.environment}</td>
                    <td style={{ padding: '12px', borderBottom: '1px solid #ddd' }}>
                      {new Date(reservation.date).toLocaleString()}
                    </td>
                    <td style={{ padding: '12px', borderBottom: '1px solid #ddd' }}>{reservation.status}</td>
                    <td style={{ padding: '12px', borderBottom: '1px solid #ddd' }}>
                      <button onClick={() => alert(`Editando reserva de ${reservation.environment}`)}>Editar</button>
                      <button onClick={() => alert(`Cancelando reserva de ${reservation.environment}`)} style={{ marginLeft: '10px' }}>Cancelar</button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          )}
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
